.toggle {
  display: block;
  width: 24px;
  height: 18px;
  margin: 23px 0 20px auto;
  padding: 0;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .toggle {
    display: none;
  }
}
.toggle--opened {
  display: block;
  width: 24px;
  height: 18px;
  margin: 23px 0 20px auto;
  padding: 0;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .toggle--opened {
    display: none;
  }
}
.toggle .toggle__icon-close {
  display: none;
}
.toggle .toggle__icon-mobile-menu {
  display: block;
}
.toggle--opened .toggle__icon-close {
  display: block;
}
.toggle--opened .toggle__icon-mobile-menu {
  display: none;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 42px;
  }
}
@media (min-width: 1300px) {
  .main-nav__list {
    margin-top: 73px;
  }
}
.main-nav__item {
  position: relative;
  width: 280px;
  padding: 25px 20px 20px 20px;
  cursor: pointer;
}
.main-nav__item::before {
  content: "";
  position: absolute;
  width: 320px;
  height: 1px;
  top: 1px;
  left: -20px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    width: auto;
    margin-left: 25px;
    padding: 0;
  }
  .main-nav__item:last-child {
    padding-right: 0;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    margin-left: 22px;
  }
}
.main-nav__link {
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link--active:hover {
  opacity: 1;
}
.main-nav__link--active:active {
  opacity: 1;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 25px;
    font-weight: 500;
  }
  .main-nav__link--active {
    position: relative;
  }
  .main-nav__link--active::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: -10px;
    left: 0;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1300px) {
  .main-nav__link {
    font-size: 20px;
    line-height: 25px;
    font-weight: 500;
  }
  .main-nav__link--index {
    color: #ffffff;
  }
  .main-nav__link--index::before {
    background: #ffffff;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav--opened .main-nav__item {
    display: block;
  }
}
