}
@media (min-width: 1300px) {
  .first-screen__center-wrapper {
    max-width: 1300px;
    padding: 0 40px;
  }
}
.first-screen__center-wrapper::before {
  content: "";
  position: absolute;
  width: 320px;
  height: 305px;
  top: 0;
  left: 0;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: -3;
}
.first-screen__center-wrapper::after {
  content: "";
  position: absolute;
  width: 320px;
  height: 305px;
  top: 0;
  left: 0;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: -2;
}
@media (min-resolution: 1.5dppx) {
  .first-screen__center-wrapper::after {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .first-screen__center-wrapper {
    min-height: 864px;
    padding-top: 54px;
    background-image: none;
  }
  .first-screen__center-wrapper::before {
    display: none;
  }
  .first-screen__center-wrapper::after {
    display: none;
  }
}
@media (min-width: 1300px) {
  .first-screen__center-wrapper {
    min-height: 580px;
    padding-top: 105px;
  }
  .first-screen__center-wrapper::before {
    display: block;
    content: "";
    position: absolute;
    width: 720px;
    height: 696px;
    top: -118px;
    left: 651px;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: center left;
    z-index: -1;
  }
  .first-screen__center-wrapper::after {
    display: block;
    content: "";
    position: absolute;
    width: calc(50vw - 15px);
    height: 696px;
    top: -118px;
    left: 50%;
    background-color: #64a83b;
    background-image: none;
    z-index: -2;
  }
}
@media (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .first-screen__center-wrapper::before {
    background-image: url("../img/index-background-desktop@2x.jpg");
    background-size: 720px auto;
  }
}
